<template>
	<view class="bg-white">
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">修改密码</block>
		</cu-custom>
		<view class="main-content-height flex align-center">
			<view class="flex flex-direction  " style="width: 100%;">
				<uni-forms :rules="rules" :value="current" ref="form" validate-trigger="bind" err-show-type="undertext">
					<password-input @input="changepassword" placeholder="请输入旧密码"></password-input>
					<repassword @input="changerePassword" password_placeholder="请输入新密码" repassword_placeholder="请再次输入新密码"></repassword>
				</uni-forms>
				<!-- 重置密码 -->
				<button class="margin padding-lg bg-cyan cu-btn round" @click="resetPassword('form')">重置密码</button>
				<!-- 跳转登录 -->
				<view class="text-cyan title flex justify-end ">
					<navigator url="/pages/login/password/password_forget?preview=true&logined=true" class="basis-sm text-right padding-lg title text-cyan" >忘记密码</navigator>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	/**
	 * 修改密码
	 * @author 陆彦捷
	 * @description  根据旧密码和新密码，进行密码修改
	 * @date 2021.06.17
	 * @date 2021.07.23 重新引用login
	 */
	var _self;
	export default {
		data() {
			return {
				// 其他参数
				preview: false, // 是否包含上一页(用于返回上一页)
				// 当前账户
				Token : this.global.login.getToken(),		// 当前Token
				current: {
					oldPwd: '', // 原始密码
					newPwd: '', // 新密码
				},
				rules: {},
				passwordvalidation: null, // 原始密码颜值
				newpasswordvalidation: null, // 新密码颜值
			}
		},
		onReady: function() {
			this.$refs.form.setRules(this.rules);
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);
		},
		methods: {
			// ========================================= 初始化 ======================================================
			getParams(options) {
				// 是否可返回上一页
				this.preview = options.preview != 'false' ? Boolean(options.preview) : false;
			},
			// ========================================= 监听事件 ==================================================
			/** 修改原始密码
			 * @param {Object} e
			 */
			changepassword(e){
				this.current.oldPwd = e.detail.password;
				this.passwordvalidation = e.validation;
			},
			/** 重新输入密码
			 * @param {Object} 
			 */
			changerePassword(e) {
				this.current.newPwd = e.detail.password;
				this.newpasswordvalidation = e.validation;
			},
			// =============================================== 重置密码 =========================================
			/** 验证原始密码成功
			 * @param {Object} result 成功信息
			 */
			password_success(result) {
				this.newpasswordvalidation(this.newPassword_success, this.message_erros);
			},
			/** 验证新密码成功
			 * @param {Object} result
			 */
			newPassword_success(result) {
				this.updatePwd_request();
			},
			/** 验证失败提示信息
			 * @param {Object} erros 失败信息
			 */
			message_erros(erros) {
				// 验证手机号、验证码验证失败
				if (erros.length > 0) {
					uni.showToast({
						title: erros[0].errorMessage,
						icon: 'none'
					});
					return;
				}
			},
			/** 重置密码
			 * @param {Object} form 提交的表单
			 */
			resetPassword(form) {
				this.passwordvalidation(this.password_success, this.message_erros);
			},
			// ============================================== 请求 =====================================================
			/**
			 * 修改密码请求
			 */
			updatePwd_request() {
				uni.showLoading();
				uni.request({
					url: _self.website + '/api/SvcUser/UpdatePwd?oldPwd='+ _self.current.oldPwd + '&newPwd=' + _self.current.newPwd,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'POST',
					data: _self.current,
					success: function(result) {
						uni.hideLoading();
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0" && result.data.Data) {
								// 修改密码成功提示信息
								uni.showToast({
									title:'修改密码成功',
								});
							} else {
								uni.showToast({
									title: result.data.ResultMsg,
									icon: 'none'
								});
							}
						} else {
							uni.showToast({
								title: '服务器连接失败！',
								icon: 'none'
							});
						}
					},
					fail: function(erros) {
						uni.hideLoading();
						uni.showToast({
							title: erros.errMsg,
							icon: 'none'
						});
					},
					complete: function() {}
				});
			}
		}
	}
</script>

